import {
  GET_HISTORY_MAIN,
  GET_BALL_HISTORY_WINNERS,
  GET_MONEY_OF_GAME_DATA,
} from "client/api/main";
import clientUtils from "../utils/client-utils";

export default {
  getHistoryMain() {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi(
          "get",
          `${GET_HISTORY_MAIN}?limit=20&page=0&sortby=id:desc`,
          {}
        )
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  getBallHistoryWinners() {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi(
          "get",
          `${GET_BALL_HISTORY_WINNERS}?limit=10&page=0&sortby=id:desc`,
          {}
        )
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  getMoneyOfGame() {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi("get", GET_MONEY_OF_GAME_DATA, {})
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
};
