import React from 'react'
import './style.scss'
import PropTypes from 'prop-types';
import { Select } from 'antd';
const { Option } = Select;

const Selected = ({
    lable,
    data,
    onUpdateSelected,
    selected,
    hiddenDefa
}) => {
    return (
        <div className="inp_box_b ">
            <label className="label-input f-16">
                {lable || ''}
            </label>
            <Select
                className="select-search-bank mt-1"
                showSearch
                optionFilterProp="children"
                filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                filterSort={(optionA, optionB) =>
                    optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())
                }
                style={{ fontSize: 14 }}
                defaultValue={'0'}
                onChange={e => onUpdateSelected(e)}
                {...selected}
            >
                {
                    !hiddenDefa && (
                        <Option value={'0'}> 종류를 선택해주세요 </Option>
                    )
                }
                {
                    data?.map((item, index) => {
                        return (
                            <Option
                                value={item?.value}
                                key={String(index + item?.text)}
                            >
                                {item?.text || ''}
                            </Option>
                        )
                    })
                }
            </Select>
        </div >
    )
}


Selected.propTypes = {
    lable: PropTypes.string,
    data: PropTypes.array,
    onUpdateSelected: PropTypes.func,
    selected: PropTypes.object,
    hiddenDefa: PropTypes.bool
};
export default Selected