import React from 'react'
import PropTypes from 'prop-types';

import { getColorFollowValue } from 'helper/game'
import './style.scss'




const ItemBall = (
    value
) => {
    
    return (
        <div
            className="item-ball-main flex-center mb-1 mr-1"
            style={{ borderColor: getColorFollowValue(value?.value) }}
        >
            <span className="item-ball">{value?.value}</span>
        </div>
    )
}


ItemBall.propTypes = {
    value: PropTypes.number
};

export default ItemBall