import moment from "moment";
import userProvider from "data-access/user-provider";
import clientUtils from "utils/client-utils";
import snackbar from "utils/snackbar-utils";
import { convertTimeUTCtoLocal } from "helper/helper";
import { QUESTION_SELECT } from "config/common";
const { location } = window;

export default {
  state: {
    auth: (() => {
      try {
        let data = localStorage.getItem("auth") || {};
        if (data) {
          data = JSON.parse(data);
          clientUtils.auth = "Bearer " + data.token;
          return data;
        }
      } catch (error) {
        console.log(error);
      }
      return null;
    })(),
    balance: 0,
    question: {
      count: 0,
      rows: [],
    },
    admin: {},
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    onRegister: (payload = {}) => {
      const { password = "", ol_password = "" } = payload || {};

      if (password !== ol_password) {
        snackbar.show("비밀번호가 일치하지 않습니다", "danger");
        return;
      }

      delete payload["ol_password"];
      return new Promise((resolve, reject) => {
        userProvider
          .register(payload)
          .then((res) => {
            if (res?.user?.id) {
              return resolve(true);
            }

            snackbar.show(res?.message || "등록 실패!", "danger");
            return resolve(false);
          })
          .catch((e) => {
            snackbar.show(e.message || "등록 실패!", "danger");
            reject(false);
          });
      });
    },
    onLogin: (payload = {}) => {
      const { password, username, saveMe = false } = payload;
      if (saveMe)
        localStorage.setItem("accountMe", JSON.stringify({ ...payload }));

      return new Promise((resolve, reject) => {
        userProvider
          .login(password, username)
          .then((res) => {
            const { user = {}, tokens = {}, message = "" } = res;

            if (user?.id) {
              const token = tokens?.access?.token || "";

              clientUtils.auth = `Bearer ${tokens?.access?.token || ""}`;
              localStorage.setItem(
                "auth",
                JSON.stringify({ ...user, token: token })
              );

              dispatch.auth.updateData({ auth: { ...user, token: token } });
              location.href = "/";
              snackbar.show(message || "로그인 성공", "success");
              resolve(true);
              return;
            }

            resolve(false);
            snackbar.show(message || "로그인 실패", "danger");
          })
          .catch((e) => {
            snackbar.show(e.message || "로그인 실패", "danger");
            reject(false);
          });
      });
    },
    onLogout: () => {
      dispatch.auth.updateData({
        auth: null,
        detail: null,
      });
      clientUtils.auth = null;
    },
    getUserInfo(payload, state) {
      return new Promise((resolve, reject) => {
        userProvider
          .getUserInfo()
          .then((res) => {
            if (res?.id) {
              const token = state?.auth?.auth?.token || "";

              localStorage.setItem(
                "auth",
                JSON.stringify({ ...res, token: token })
              );
              dispatch.auth.updateData({ balance: res?.balance || 0 });
              return;
            }
          })
          .catch((e) => {});
      });
    },
    checkRole: (payload, state) => {
      let auth = state.auth.auth;
      if (auth && auth.id) {
        let checkRole = (auth.authorities || []).includes("ROLE_Manager");
        dispatch.auth.updateData({
          checkRole,
        });
      }
    },
    onChangePassword(payload = {}) {
      const {
        olPassword = "",
        password = "",
        confirmPassword = "",
      } = payload || {};
      if (password !== confirmPassword) {
        snackbar.show("새 비밀번호가 일치하지 않습니다!", "danger");
        return;
      }

      if (
        !(
          password.length > 7 &&
          password.length > 7 &&
          confirmPassword.length > 7
        )
      ) {
        return snackbar.show("비밀번호는 8자 이상이여야 합니다!", "danger");
      }

      const body = {
        oldpassword: olPassword,
        password: password,
      };
      return new Promise((resolve, reject) => {
        userProvider
          .changePassWord(body)
          .then((res) => {
            const { message = "" } = res;

            if (res.issuccess) {
              snackbar.show("비밀번호 변경 성공", "success");
            } else {
              snackbar.show(message || "비밀번호 변경 실패", "danger");
            }

            resolve(true);
          })
          .catch((e) => {
            snackbar.show(e.message || "비밀번호 변경 실패", "danger");
            reject(false);
          });
      });
    },
    getQuestionData() {
      return new Promise((resolve, reject) => {
        userProvider
          .getQuestionData()
          .then((res) => {
            if (res?.count) {
              res.rows = (res?.rows || []).map((item) => {
                const textCategory = QUESTION_SELECT.find(
                  (_i) => _i.id == item?.questioncategoryid
                );
                const dateUser = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );
                const dateAnswer = convertTimeUTCtoLocal(
                  item?.updatedat || new Date()
                );

                return {
                  ...item,
                  timeConvert: moment(dateUser).format("YYYY.MM.DD.HH:mm"),
                  timeConvertAnswer:
                    moment(dateAnswer).format("YYYY.MM.DD.HH:mm"),
                  textCategory: textCategory?.text || "",
                };
              });

              dispatch.auth.updateData({ question: res });
            }
          })
          .catch((e) => {});
      });
    },
    onCreateQuestion(payload = {}, state) {
      return new Promise((resolve, reject) => {
        userProvider
          .createdQuestionFromUser(payload)
          .then((res) => {
            if (res?.id) {
              let question = state.auth.question;
              const textCategory = QUESTION_SELECT.find(
                (_i) => _i.id == res?.questioncategoryid
              );

              question.count++;
              question.rows.unshift({
                ...res,
                timeConvert: moment().format("YYYY.MM.DD.HH:mm"),
                textCategory: textCategory?.text || "",
              });
              dispatch.auth.updateData({ question: { ...question } });
              snackbar.show("문의가 등록되었습니다.", "success");
            } else {
              const { message = "" } = res;

              snackbar.show(message || "", "danger");
            }
            resolve(true);
          })
          .catch((e) => {
            snackbar.show(e.message || "", "danger");
            reject(false);
          });
      });
    },
    getDataAdminMessage() {
      return new Promise((resolve, reject) => {
        userProvider
          .getDataAdminMessage()
          .then((res) => {
            if (res?.count) {
              res.rows = (res?.rows || []).map((item) => {
                const dateUser = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );

                return {
                  ...item,
                  timeConvert: moment(dateUser).format("YYYY.MM.DD.HH:mm"),
                };
              });

              dispatch.auth.updateData({ admin: res });
            }
          })
          .catch((e) => {});
      });
    },
    onSetStatusAdminMess(payload) {
      return new Promise((resolve, reject) => {
        userProvider
          .setStatusMessage(payload)
          .then((res) => {
            if (res?.id) return resolve();
            return resolve();
          })
          .catch((e) => {
            reject();
          });
      });
    },
  }),
};
