import client from "utils/client-utils";
import {
  GET_RESULT_POWERBALL,
  GET_HISTORY_BET_POWERBALL,
  BET_GAME_POWER_BALLARRAY,
  GET_MONEY_OF_GAME_BY_ID,
  BET_GAME_POWER_BALLARRAY_5MINUTES,
} from "client/api/game";

export default {
  getHistoryPowerball(gameid = 1) {
    return client.requestApi(
      "get",
      `${GET_HISTORY_BET_POWERBALL}?limit=20&page=0&sortby=id:desc&gameid=${gameid}`,
      {}
    );
  },
  getResultPowerball(body = {}) {
    
    const url = `${GET_RESULT_POWERBALL}?limit=${
      body?.pageSize ? body.pageSize : 1
    }&page=0&sortby=id:desc&gametypeid=${body?.gametypeid || 1}`;

    return client.requestApi("get", url, {});
  },
  getInfoMoneyByGameId(gameid = 1, gametypeid = 1) {
    return client.requestApi(
      "get",
      `${GET_MONEY_OF_GAME_BY_ID}?limit=1&page=0&sortby=id:desc&gameid=${gameid}&gametypeid=${gametypeid}`,
      {}
    );
  },
  betGamePowerball(body = {}, typeAPI = "0", gametypeid = 1) {
    const url =
      gametypeid == 3
        ? BET_GAME_POWER_BALLARRAY_5MINUTES
        : BET_GAME_POWER_BALLARRAY;

    return client.requestApi("post", `${url}${typeAPI}`, body);
  },
};
