import React, { useState, useEffect } from 'react'
import { connect } from "react-redux"
import { Checkbox, Form } from 'antd';
import {
    LayoutLottery,
    BoxLottery,
    TitleBox,
    InputLable,
    ButtonLottery
} from "components";
import "./style.scss";
// import { isArray } from 'lodash'
const { document } = window



const Login = ({
    onLogin
}) => {
    const [loading, SetLoading] = useState(false)
    const [accountMe, SetAccountMe] = useState(false)
    useEffect(() => {
        try {
            let data = localStorage.getItem("accountMe") || {};
            if (data) {
                data = JSON.parse(data || JSON.stringify({}));

                SetAccountMe(data || {})
            }
        } catch (error) {
            console.log(error);
        }
    }, [])


    const onFinish = async () => {
        SetLoading(true)


        await onLogin({ ...accountMe }).then(res => { }).catch(err => { })
        SetLoading(false)
    }
    return (
        <LayoutLottery>
            <div className="page-login">
                <div className="full-screen flex-center">
                    <div className="container-cst flex-center">
                        <BoxLottery
                            style={{ width: '100%', display: 'flex' }}
                            span={11}
                            wFull
                        >
                            <div className="pt-5 pr-6 pl-6 pb-3 mob-login">
                                <TitleBox name="로그인" />
                                <div className="content">
                                    <Form
                                        name="basic"
                                        layout="vertical"
                                        initialValues={{
                                            remember: true,
                                        }}
                                        onFinish={onFinish}
                                    >
                                        <Form.Item
                                            name="username"
                                            className="mb-075"
                                            rules={[{
                                                required: accountMe?.username ? false : true,
                                                message: '이 필드는 비워둘 수 없습니다!',
                                            }]}
                                        >
                                            <div>
                                                <InputLable
                                                    lable="아이디"
                                                    input={{
                                                        placeholder: " ",
                                                        readOnly: loading,
                                                        value: accountMe?.username || '',
                                                        onChange: (e) => SetAccountMe({ ...accountMe, username: e?.target?.value || '' })
                                                    }}
                                                />
                                            </div>
                                        </Form.Item>
                                        <Form.Item
                                            name="password"
                                            className="mb-075"
                                            rules={[{
                                                required: accountMe?.password ? false : true,
                                                message: '이 필드는 비워둘 수 없습니다!'
                                            }]}
                                        >
                                            <div>
                                                <InputLable
                                                    lable="비밀번호"
                                                    input={{
                                                        type: "password",
                                                        placeholder: "",
                                                        maxLength: 16,
                                                        readOnly: loading,
                                                        value: accountMe?.password || '',
                                                        onChange: (e) => SetAccountMe({ ...accountMe, password: e?.target?.value || '' })
                                                    }}
                                                />
                                            </div>
                                        </Form.Item>



                                        <Form.Item name="saveMe">
                                            <Checkbox
                                                checked={accountMe?.saveMe || false}
                                                onChange={checked => SetAccountMe({
                                                    ...accountMe,
                                                    saveMe: checked?.target?.checked || false
                                                })}
                                            >
                                                <span style={{ color: '#000' }}> 자동 로그인</span>
                                            </Checkbox>
                                        </Form.Item>
                                        <Form.Item>
                                            <div className="flex-center justify-between">
                                                <ButtonLottery
                                                    text="회원가입"
                                                    bntHref
                                                    primary
                                                    button={{
                                                        href: "/register",
                                                        style: { marginRight: '0.5rem' }
                                                    }}
                                                />
                                                <ButtonLottery
                                                    text="로그인"
                                                    button={{
                                                        htmlType: "submit",
                                                        style: { marginLeft: '0.5rem' },
                                                        loading: loading
                                                    }}
                                                />
                                            </div>

                                        </Form.Item>
                                    </Form>
                                </div>
                            </div>
                        </BoxLottery>
                    </div>
                </div>


            </div>

        </LayoutLottery>
    )
}

export default connect(
    null,
    ({ auth: { onLogin } }) => {
        return {
            onLogin
        }
    }
)(Login);
