import {
  INQUIRE_MY_LOTTO,
  INQUIRE_MY_TRANSACTION,
  REQUEST_WITHDRAW_MONEY,
  REQUEST_DEPOSIT_MONEY,
} from "client/api/history";
import clientUtils from "../utils/client-utils";
// var md5 = require('md5');

export default {
  getMyInquireLotto(payload = {}) {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi(
          "get",
          `${INQUIRE_MY_LOTTO}?limit=${payload?.pageSize || 10}&page=${
            payload?.page || 0
          }&sortby=id:desc${
            payload?.gameid ? "&gameid=" + payload?.gameid : ""
          }`,
          {}
        )
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  getMyInquireTransaction(payload = {}) {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi(
          "get",
          `${INQUIRE_MY_TRANSACTION}?limit=${payload?.pageSize || 10}&page=${
            payload?.page || 0
          }&sortby=id:desc`,
          {}
        )
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  requestDeposit(body = {}) {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi("post", REQUEST_DEPOSIT_MONEY, body)
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  requestWithdraw(body = {}) {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi("post", REQUEST_WITHDRAW_MONEY, body)
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
};
