import powerballProvider from "data-access/game/powerball-provider";
import moment from "moment";
import { convertTimeUTCtoLocal } from "helper/helper";
import {
  convertItemSelected,
  randomNumberMinMax,
  getGameIdNumber,
  getGameTypeID,
  converDataHistoryExaclyGame,
} from "helper/game";
import snackbar from "utils/snackbar-utils";

export default {
  state: {
    history: [],
    result: {},
    orderItem: [],
    infoGame: {},
    loading: false,
    endRoundOfGame: "",

    //for page main
    listResult: [],
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    reLoadDataGame(payload = {}) {
      const page = payload?.page || "";

      dispatch.powerball.onGetInfoGame({ ...payload });
      switch (page) {
        case "MAIN": {
          dispatch.powerball.getListRsultGame({ ...payload });
          break;
        }
        default: {
          dispatch.powerball.getDataHistory({ ...payload });
          dispatch.powerball.getResult({ ...payload });
          break;
        }
      }
    },
    getResult(payload = {}) {
      payload.gametypeid = getGameTypeID({ ...payload });

      return new Promise((resolve, reject) => {
        powerballProvider
          .getResultPowerball({ ...payload })
          .then((res) => {
            if (res?.count) {
              const result = res?.rows[0] || {};

              dispatch.powerball.updateData({
                result: {
                  ...result,
                  data: [
                    result?.number1 || 0,
                    result?.number2 || 0,
                    result?.number3 || 0,
                    result?.number4 || 0,
                    result?.number5 || 0,
                    result?.number6 || 0,
                  ],
                },
              });
            }

            resolve(true);
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
    getDataHistory(payload = {}) {
      const type = getGameIdNumber({ ...payload, callAPI: "HISTORY" });

      return new Promise((resolve, reject) => {
        powerballProvider
          .getHistoryPowerball(type)
          .then((res) => {
            if (res?.count) {
              const history = (res?.rows || []).map((item) => {
                const dateToLocal = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );
                const _resultArr = [
                  item?.resultofgame?.number1 || 0,
                  item?.resultofgame?.number2 || 0,
                  item?.resultofgame?.number3 || 0,
                  item?.resultofgame?.number4 || 0,
                  item?.resultofgame?.number5 || 0,
                  item?.resultofgame?.number6 || 0,
                  item?.resultofgame?.number7 || 0,
                ];
                const _fullbetArr = String(item?.fullbet || "").split(",");
                const exacly = converDataHistoryExaclyGame(
                  _fullbetArr,
                  _resultArr
                );

                return {
                  ...item,
                  timeConvert: moment(dateToLocal).format("HH:mm"),
                  exacly: exacly,
                };
              });

              dispatch.powerball.updateData({
                history: history,
              });
            }
            resolve(true);
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
    onRandomNumberMinMax(isRamdom = true) {
      const option = [];

      while (option.length < 6) {
        if (option.length < 5) {
          const num = randomNumberMinMax(1, 27);
          if (!option.find((_i) => _i.value == num)) {
            option.push({ value: num });
          }
        } else option.push({ value: randomNumberMinMax(0, 9) });
      }

      dispatch.powerball.onSentResultOptions({
        isRamdom: isRamdom,
        option: option,
      });
    },
    onSentResultOptions(result = {}, state) {
      return new Promise((resolve, reject) => {
        const { orderItem = [] } = state?.powerball || {};
        const leg = (orderItem || []).length;
        if (leg > 4) return snackbar.show("5개까지 선택 가능합니다.", "danger");

        const { option = [], isRamdom = false } = result || {};
        if ((option || []).length < 6)
          return snackbar.show("6개의 숫자를 선택해주세요.", "danger");

        const item = convertItemSelected(option, isRamdom, leg);
        dispatch.powerball.updateData({
          orderItem: [...orderItem, ...[item]],
        });
        snackbar.show("구매 대기열에 추가되었습니다.", "success");
        return resolve(isRamdom ? false : true);
      });
    },
    onRemoveItemSelectd(id, state) {
      const { orderItem = [] } = state?.powerball || {};
      const newOrederItem = orderItem.filter((_i) => _i.orderId !== id);

      dispatch.powerball.updateData({
        orderItem: [...newOrederItem],
      });
    },
    resetOrderAndUpdateHistory(payload = {}, state) {
      const { items = [], history = [], type = 0 } = payload;
      const price = type == 1000005 ? 10000 : type;
      const auth = state?.auth || {};
      const arr = [...history];

      items.map((_his) => {
        arr.unshift({
          ..._his,
          timeConvert: moment().format("HH:mm"),
          username: auth?.auth?.username || "",
        });
      });

      dispatch.powerball.updateData({
        orderItem: [],
        history: [...arr],
      });
      const balance = (auth?.balance || 0) - (items?.length || 0) * price;
      dispatch.auth.updateData({
        balance: balance,
      });
    },
    //get info
    onGetInfoGame(payload = {}) {
      const type = getGameIdNumber({ ...payload, callAPI: "INFO_GAME" });
      const gameTypeID = getGameTypeID({ ...payload });

      return new Promise((resolve, reject) => {
        powerballProvider
          .getInfoMoneyByGameId(type, gameTypeID)
          .then((res) => {
            if (res?.id) {
              const dateToLocal = moment(
                convertTimeUTCtoLocal(res?.endroundofgame || new Date())
              ).format("MM-DD-YYYY HH:mm:ss");
              // const timer = moment("12/17/2021 06:00:00").format(
              //   "MM-DD-YYYY HH:mm:ss"
              // );

              dispatch.powerball.updateData({
                infoGame: res,
                endRoundOfGame: dateToLocal,
              });

              resolve();
            }
          })
          .catch(reject);
      });
    },
    //bet game
    onBetGameOrder(_payload = {}, state) {
      const typeAPI = getGameIdNumber({ ..._payload, callAPI: "BETGAME" });
      const gametypeid = getGameTypeID({ ..._payload });
      const gameid = getGameIdNumber({ ..._payload });
      const { orderItem = [], history = [] } = state?.powerball || {};
      const payload = orderItem.map((item) => {
        delete item["result"];
        delete item["type"];
        delete item["orderId"];

        return { ...item };
      });

      dispatch.powerball.updateData({ loading: true });
      const body = { items: payload, history: history };
      return new Promise(() => {
        const items = { items: [...(payload || [])] };

        powerballProvider
          .betGamePowerball(items, typeAPI, gametypeid)
          .then((res) => {
            dispatch.powerball.updateData({ loading: false });
            const { issuccess = false } = res || {};

            if (issuccess) {
              snackbar.show("주문이 완료되었습니다.", "success");

              dispatch.powerball.resetOrderAndUpdateHistory(
                { ...body, type: Number(_payload?.typeGame || 5000) },
                { ...state }
              );
              dispatch.history.getMyInquireLotto({ gameid: gameid });
              return;
            }

            snackbar.show("주문에 실패했습니다!", "danger");
          })
          .catch(() => {
            snackbar.show(
              "오류가 발생했습니다. 잠시 후 다시 시도해주세요!",
              "danger"
            );
            dispatch.powerball.updateData({ loading: false });
          });
      });
    },

    // for page main
    getListRsultGame(payload) {
      return new Promise((resolve, reject) => {
        powerballProvider
          .getResultPowerball({ ...payload })
          .then((res) => {
            resolve(true);

            if (res?.count) {
              const arr = [];
              const result = res?.rows || [];

              result.map((item) => {
                arr.push({
                  ...item,
                  totalBall: 6,
                  data: [
                    item?.number1 || 0,
                    item?.number2 || 0,
                    item?.number3 || 0,
                    item?.number4 || 0,
                    item?.number5 || 0,
                    item?.number6 || 0,
                  ],
                });
              });

              dispatch.powerball.updateData({
                listResult: arr,
              });
            }
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
  }),
};
