import React from 'react'
import PropTypes from 'prop-types';

import { renderNameStar } from 'helper/game'
import './style.scss'




const ItemText = ({
    data
}) => {

    const render = data.map((_, ind) => <span key={String(ind + _)} className={`item-text key-${ind}`}>{_ || ''}</span>)
    return (
        <div className="item-text-main flex-center justify-between pb-1">
            {render}
        </div>
    )
}


ItemText.propTypes = {
    data: PropTypes.array
};

export default ItemText