import React from "react";
import Header from "../Header";
import PropTypes from "prop-types";
import { REACT_DOMAIN } from "client/request";
const { location } = window;

let isLogin =
  String(location.href).indexOf("/login") > -1 ||
  String(location.href).indexOf("/register") > -1 ||
  String(location.href) == String(`${REACT_DOMAIN}/`)
    ? true
    : false;
const isPage = localStorage.getItem("auth") ? true : false;
if (isPage && String(location.href).length == 22) isLogin = false;
if (isLogin && String(location.href).length !== 22)
  localStorage.removeItem("auth");

const Layout = ({ children }) => {
  if (!isLogin && !isPage) location.href = "/login";

  return (
    <>
      <Header isLogin={!isLogin} />
      <main>{children}</main>
    </>
  );
};

Layout.propTypes = {
  children: PropTypes.any.isRequired,
};

export default Layout;
