import mainProvider from "data-access/main-provider";
import {
  renderNameStar,
  getShowNameFollowGameId,
  getTypeGame,
} from "helper/game";
import { formatMoney } from "helper/helper";

export default {
  state: {
    dataHistory: [],
    winners: [],
    dataMoneyGame: [],
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    getHistoryMain: () => {
      return new Promise((resolve, reject) => {
        mainProvider
          .getHistoryMain()
          .then((res) => {
            resolve();
            if (res?.count) {
              const arr = (res?.rows || []).map((item) => {
                const data = [
                  `${
                    item.gameid == 9
                      ? "파워볼"
                      : item.gameid <= 4
                      ? "파워볼"
                      : "로또"
                  }${item?.price}`,
                  renderNameStar(item?.username),
                  item.fullbet.replaceAll(",", ", "),
                ];
                return {
                  ...item,
                  data,
                };
              });

              dispatch.main.updateData({ dataHistory: [...arr] });
            }
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
    getBallHistoryWinners: () => {
      return new Promise((resolve, reject) => {
        mainProvider
          .getBallHistoryWinners()
          .then((res) => {
            resolve();
            if (res?.count) {
              const arr = (res?.rows || []).map((item) => {
                const row2 = `${item?.resultofgame?.roundofdate || 0}${
                  (item?.resultofgame?.drawno &&
                    `(${item?.resultofgame?.drawno || 0})회`) ||
                  ""
                }`;
                const data = [
                  // 1
                  getShowNameFollowGameId(item?.gameid || 1),
                  // 2
                  row2,
                  // 3
                  item?.username || "",
                  // 4
                  `${formatMoney(Number(item?.winmoney || 0))}원`,
                ];

                return {
                  data: data,
                };
              });

              dispatch.main.updateData({ winners: [...arr] });
            }
          })
          .catch((e) => {
            reject();
          });
      });
    },
    getMoneyOfGame: () => {
      return new Promise((resolve, reject) => {
        mainProvider
          .getMoneyOfGame()
          .then((res) => {
            if (res?.length) {
              const data = res.map((item) => {
                return {
                  ...item,
                  amount: Number(item?.totalmoney),
                  type: getTypeGame(item?.gameid || 0),
                  name_game: getTypeGame(item?.gameid || 0, "NAME_GAME"),
                };
              });

              dispatch.main.updateData({ dataMoneyGame: [...data] });
              resolve();
            }
            reject();
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
  }),
};
