import moment from "moment";

import historyProvider from "data-access/history-provider";
import {
  convertTimeUTCtoLocal,
  formatMoney,
  hanldNumberFormat,
} from "helper/helper";
import { converDataHistoryExaclyGame } from "helper/game";
import { STATUS_GAME, TYPE_TRANSAC, STATUS_TRANSAC } from "config/togger";
import snackbar from "utils/snackbar-utils";
import _default from "@ant-design/icons/lib/icons/AccountBookFilled";

export default {
  state: {
    InquireLotto: [],
    InquireTransaction: [],
    resetForm: false,
    count: 0,
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    getMyInquireLotto: (payload = {}) => {
      const page = payload?.pageIndex || 1;

      return new Promise((resolve, reject) => {
        historyProvider
          .getMyInquireLotto({ ...payload, page: page - 1 })
          .then((res) => {
            if (res?.count) {
              const { rows = [], count = 0 } = res || {};
              const data = [{}];

              rows.map((item, index) => {
                const dateToLocal = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );
                const _resultArr = [
                  item?.resultofgame?.number1 || 0,
                  item?.resultofgame?.number2 || 0,
                  item?.resultofgame?.number3 || 0,
                  item?.resultofgame?.number4 || 0,
                  item?.resultofgame?.number5 || 0,
                  item?.resultofgame?.number6 || 0,
                  item?.resultofgame?.number7 || 0,
                ];
                const _fullbetArr = String(item?.fullbet || "").split(",");
                const exacly = converDataHistoryExaclyGame(
                  _fullbetArr,
                  _resultArr
                );

                data.push({
                  ...item,
                  key: index + 1,
                  timeConvert: moment(dateToLocal).format("YYYY.MM.DD.HH:mm"),
                  roundText: `${item?.roundofgame || 0}회차`,
                  winMoneyText: `${formatMoney(Number(item?.winmoney || 0))}원`,
                  priceText: formatMoney(item?.price || 0),
                  fullbet: String(item?.fullbet || "").replaceAll(",", ", "),
                  typeGame:
                    item?.gameid < 5 || item?.gameid == 9 || item?.gameid == 10
                      ? "파워볼"
                      : "로또",
                  statusTxt: STATUS_GAME[item?.status],
                  exacly: exacly,
                });
              });

              dispatch.history.updateData({
                InquireLotto: data,
                count: count,
              });
            }

            return resolve(true);
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
    getMyInquireTransaction: (payload = {}) => {
      const page = payload?.pageIndex || 1;

      return new Promise((resolve, reject) => {
        historyProvider
          .getMyInquireTransaction({ ...payload, page: page - 1 })
          .then((res) => {
            if (res?.count) {
              const { rows = [], count = 0 } = res || {};
              const data = [{}];

              rows.map((item, index) => {
                const dateToLocal = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );

                data.push({
                  ...item,
                  key: index + 1,
                  timeConvert: moment(dateToLocal).format("YYYY.MM.DD.HH:mm"),
                  winMoneyText: `${formatMoney(Number(item?.amount || 0))}원`,
                  statusTxt: STATUS_TRANSAC[item?.status],
                  typeTxt: TYPE_TRANSAC[item?.transactiontype],
                });
              });

              dispatch.history.updateData({
                InquireTransaction: [...data],
                count: count,
              });
            }

            return resolve(true);
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
    onRequestWithdraw(payload = {}) {
      payload.amountmoneywithdraw = hanldNumberFormat(
        payload.amountmoneywithdraw
      );
      return new Promise((resolve, reject) => {
        historyProvider
          .requestWithdraw(payload)
          .then((res) => {
            const { message = "" } = res || {};

            resolve();
            if (res?.id) {
              dispatch.history.updateData({
                resetForm: true,
              });
              return snackbar.show(
                "관리자에게 철회 요청이 전송되었습니다. 도움이 필요하면 관리자에게 문의하십시오.",
                "success"
              );
            }

            return snackbar.show(message || "출금 요청 실패!", "danger");
          })
          .catch((e) => {
            snackbar.show("주문에 실패했습니다!", "danger");
            reject();
          });
      });
    },
    onRequestDeposit(payload = {}) {
      payload.amountmoneydeposit = hanldNumberFormat(
        payload.amountmoneydeposit
      );
      return new Promise((resolve, reject) => {
        historyProvider
          .requestDeposit(payload)
          .then((res) => {
            resolve(true);
            const { message = "" } = res || {};

            if (res?.id) {
              dispatch.history.updateData({
                resetForm: true,
              });
              return snackbar.show(
                "입금 요청이 관리자에게 전송되었습니다. 도움이 필요하면 관리자에게 문의하세요.",
                "success"
              );
            }
            return snackbar.show(message || "입금 요청 실패!", "danger");
          })
          .catch((e) => {
            snackbar.show("주문에 실패했습니다!", "danger");
            reject(false);
          });
      });
    },
    onUpdateData(payload) {
      dispatch.history.updateData({
        resetForm: payload?.resetForm,
      });
    },
  }),
};
