// export default Countdown;

import React, { useState, useEffect } from "react";

import PropTypes from "prop-types";
// import './style.css';

const Countdown = ({ dateTime, callback, type }) => {
  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);
  const [timeUp, setTimeUp] = useState(false);

  function calcDifference() {
    let eventDate = +new Date(dateTime);
    let difference = eventDate - +new Date();
    if (difference < 1) {
      setTimeUp(true);
      callback();
    } else {
      setTimeUp(false);
      setDays(Math.floor(difference / (1000 * 60 * 60 * 24)));
      setHours(Math.floor((difference / (1000 * 60 * 60)) % 24));
      setMinutes(Math.floor((difference / (1000 * 60)) % 60));
      setSeconds(Math.floor((difference / 1000) % 60));
    }
  }

  useEffect(() => {
    calcDifference();
    const interval = setInterval(() => {
      calcDifference();
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [dateTime]);

  // if (timeUp)
  //   return (
  //     <span style={{ fontWeight: 600, color: "#000000" }}>{`00 : 00`}</span>
  //   );

  const showTimer = () => {
    // 일 year, 시간 month, 분 day
    const _minutes =
      minutes && Number(minutes) > 9 ? minutes : `0${minutes}` || "00";
    const _seconds =
      seconds && Number(seconds) > 9 ? seconds : `0${seconds}` || "00";
    const _hours = hours && Number(hours) > 9 ? hours : `0${hours}` || "00";

    switch (type) {
      case "LOTTO": {
        const timer = Number(days) > 0 ? String(days + "일") : "";
        return `${timer} ${_hours}시간 ${_minutes}분`;
      }
      case "POWERBALL": {
        const timer = Number(hours) > 0 ? String(_hours + "시간") : "";

        return `${timer} ${_minutes} : ${_seconds}`;
      }
    }
  };
  return (
    <span style={{ fontWeight: 600, color: "#000000" }}>
      {timeUp ? `00 : 00` : showTimer()}
    </span>
  );
};

Countdown.propTypes = {
  dateTime: PropTypes.string,
  callback: PropTypes.func,
  type: PropTypes.string,
};

export default Countdown;
