import React from 'react'

import { GAME_LIST } from "config/togger";
import "./style.scss";


const SelectGame = () => {

    const renderItemListGame = GAME_LIST.map((_, i) => (
        <a className="item-game mr-2 mb-2"
            href={_?.href || '#'}
            key={String(i)}
        >
            <img className="img-icon"
                src={require(`resources/${_.img}`)}
            />
            <div className="text-content">
                <span className="f-16">
                    {_?.text || ""}
                </span>
            </div>
        </a>
    ))

    return (
        <div id="page-game-list" className="page-game-list ">
            <div className="container-cst pt-3 pb-2">
                <div className="list-content-select-game flex-center flex-wrap">
                    {renderItemListGame}
                </div>
            </div>
        </div>
    )
}

export default SelectGame;
