import React from "react";
import Loadable from "react-loadable";
import { Switch } from "react-router-dom";
import { RouterWithPaths, SettingLayout, Loading } from "components";
import "antd/dist/antd.css";

const index = (props) => {
  const routers = [
    {
      path: ["/"],
      component: Loadable({
        loader: () => import("pages/Home"),
        loading: Loading,
      }),
    },
    {
      path: ["/register"],
      component: Loadable({
        loader: () => import("pages/Register"),
        loading: Loading,
      }),
    },
    {
      path: ["/notice"],
      component: Loadable({
        loader: () => import("pages/Notice"),
        loading: Loading,
      }),
    },
    {
      path: ["/notice/detail/:id"],
      component: Loadable({
        loader: () => import("pages/Notice/component/DetailsNotice"),
        loading: Loading,
      }),
    },
    {
      path: ["/game/powerball/:type"],
      component: Loadable({
        loader: () => import("pages/Game/Powerball"),
        loading: Loading,
      }),
    },
    {
      path: ["/game/lotto/:type"],
      component: Loadable({
        loader: () => import("pages/Game/Lotto"),
        loading: Loading,
      }),
    },
    {
      path: ["/my-page"],
      component: Loadable({
        loader: () => import("pages/MyAccount"),
        loading: Loading,
      }),
    },
    {
      path: ["/history/lottrey"],
      component: Loadable({
        loader: () => import("pages/History/Lottrey"),
        loading: Loading,
      }),
    },
    {
      path: ["/history/transaction"],
      component: Loadable({
        loader: () => import("pages/History/Transaction"),
        loading: Loading,
      }),
    },
    {
      path: ["/deposit"],
      component: Loadable({
        loader: () => import("pages/Deposit"),
        loading: Loading,
      }),
    },
    {
      path: ["/withdraw"],
      component: Loadable({
        loader: () => import("pages/Withdraw"),
        loading: Loading,
      }),
    },
    {
      path: ["/require"],
      component: Loadable({
        loader: () => import("pages/Require"),
        loading: Loading,
      }),
    },
    {
      path: ["/admin-message"],
      component: Loadable({
        loader: () => import("pages/AdminMessage"),
        loading: Loading,
      }),
    },
    {
      path: ["/checking-the-system"],
      component: Loadable({
        loader: () => import("pages/CheckingInSystem"),
        loading: Loading,
      }),
    },
    {
      path: ["/landing-page/rules-winning"],
      component: Loadable({
        loader: () => import("pages/LandingPage/RulesWinning"),
        loading: Loading,
      }),
    },
  ];

  return (
    <div>
      <Switch>
        {routers.map((route, key) => {
          if (route.component)
            return (
              <RouterWithPaths
                exact
                key={key}
                path={route.path}
                render={(props) => {
                  return <route.component {...props} />;
                }}
              />
            );
          return null;
        })}
      </Switch>
      <SettingLayout />
    </div>
  );
};

export default index;
