import client from "utils/client-utils";
import {
    GET_RESULT_POWERBALL,
    GET_HISTORY_BET_POWERBALL,
    BET_GAME_LOTTO_BALLARRAY,
    GET_MONEY_OF_GAME_BY_ID
} from "client/api/game";


export default {
    getHistoryLotto(gameid = 5) {
        return client.requestApi(
            "get",
            `${GET_HISTORY_BET_POWERBALL}?limit=20&page=0&sortby=id:desc&gameid=${gameid}`,
            {}
        );
    },
    getResultLotto(body = {}) {

        const url = `${GET_RESULT_POWERBALL}?limit=${body?.pageSize ? body.pageSize : 1}&page=0&sortby=id:desc&gametypeid=2`
        return client.requestApi(
            "get",
            url,
            {}
        );
    },
    getInfoMoneyByGameId(gameid = 5, typeAPI = '05') {
        return client.requestApi(
            "get",
            `${GET_MONEY_OF_GAME_BY_ID}?limit=1&page=0&sortby=id:desc&gameid=${gameid}&gametypeid=2`,
            {}
        );
    },
    betGameLotto(body = {}, typeAPI = '0') {
        return client.requestApi(
            "post",
            `${BET_GAME_LOTTO_BALLARRAY}${typeAPI}`,
            body
        );
    },

};
