import React, { useEffect } from "react";
import { connect } from "react-redux";

import { ThemeProvider } from "styled-components";
import { pink } from "themes";
import User from "../pages";
import Auth from "../pages/Login";
import { ConfigProvider } from "antd";
// import viVN from "antd/es/locale/vi_VN";
import koKR from "antd/es/locale/ko_KR";
import RouterWithPaths from "components/RouterWithPaths";
import { Switch } from "react-router-dom";
import './styles.scss'
import { useHistory, Redirect } from "react-router-dom";

Array.prototype.insert = function (index, item) {
  this.splice(index, 0, item);
};

const App = (props) => {
  const history = useHistory();

  useEffect(() => {
    props.checkRole();
  }, []);



  const logout = connect(null, ({ auth: { updateData } }) => ({ updateData }))(
    (props) => {
      props.updateData({
        auth: null,
      });
      localStorage.removeItem("auth");
      setTimeout(() => {
        history.push("/login");
      }, 2000);
      return null;
    }
  );
  String.prototype.uintTextBox = function () {
    var re = /^\d*$/;
    return re.test(this);
  };
  const routers = [
    {
      path: ["/login"],
      component: Auth,
    },
    {
      path: ["/logout"],
      component: logout,
    },
    {
      path: [
        "/",
        "/:function1",
        "/:function1/:id",
        "/:function1/:function2/:id",
      ],
      component: User,
    },

  ];
  return (
    <ThemeProvider theme={pink}>
      <ConfigProvider locale={koKR}>
        <Switch>
          {routers.map((route, key) => {
            if (route.component)
              return (
                <RouterWithPaths
                  key={key}
                  path={route.path}
                  render={(props) => {
                    return <route.component {...props} />;
                  }}
                />
              );
            return null;
          })}
        </Switch>
      </ConfigProvider>
    </ThemeProvider>
  );
};

const mapState = (state) => ({
  auth: state.auth.auth,
});

const mapDispatch = ({
  auth: { onLogin, onLogout, updateData, loadWithToken, checkRole },
}) => ({
  onLogin,
  onLogout,
  updateData,
  loadWithToken,
  checkRole,
});

export default connect(mapState, mapDispatch)(App);
