import moment from "moment";
import { isArray } from "lodash";

import commonProvider from "data-access/common-provider";
import { convertTimeUTCtoLocal } from "helper/helper";

export default {
  state: {
    banksInfo: (() => {
      try {
        let data = localStorage.getItem("_banks") || "";
        if (data) {
          data = JSON.parse(data);
          return data;
        }
      } catch (error) {
        console.log("error_banks", error);
      }
      return null;
    })(),
    notices: {},
    noticeDetails: {},
    systemCheckingData: {},
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    onGetAllBanks: () => {
      return new Promise((resolve, reject) => {
        // localStorage.setItem("auth", JSON.stringify((data || {})));
        commonProvider
          .getAllBanks()
          .then((res) => {
            if (res?.count && isArray(res?.rows)) {
              res.rows = res.rows.map((item) => {
                return {
                  ...item,
                  value: item?.id || 0,
                  text: item?.bankname || "",
                };
              });

              dispatch.common.updateData({
                banksInfo: res,
              });
              localStorage.setItem("_banks", JSON.stringify(res || {}));
            }
          })
          .catch((e) => {
            console.log("getAllBanksError: ", e);
            reject(e);
          });
      });
    },
    onGetDataNotices: (payload) => {
      return new Promise((resolve, reject) => {
        commonProvider
          .getDataNotice(payload)
          .then((res) => {
            resolve();

            if (res?.count && isArray(res?.rows)) {
              res.rows = res.rows.map((item) => {
                const dateToLocal = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );

                return {
                  ...item,
                  dateConvert: moment(dateToLocal).format("YYYY.MM.DD"),
                  timerConvert: moment(dateToLocal).format("YYYY.MM.DD.HH:mm"),
                };
              });

              dispatch.common.updateData({
                notices: res,
              });
            }
          })
          .catch((e) => {
            reject(e);
          });
      });
    },
    onGetDetailNotice: (payload) => {
      return new Promise((resolve, reject) => {
        commonProvider
          .getDetailsNotice(payload)
          .then((res) => {
            if (res?.id) {
              const dateToLocal = convertTimeUTCtoLocal(
                res?.createdat || new Date()
              );
              res.dateConvert = moment(dateToLocal).format("YYYY.MM.DD.HH:mm");

              dispatch.common.updateData({
                noticeDetails: res,
              });
            }
          })
          .catch((e) => {});
      });
    },
    onGetDataCheckingTheOnSystem: () => {
      return new Promise((resolve, reject) => {
        commonProvider
          .getDataCheckingOnTheSystem()
          .then((res) => {
            if (res?.id) {
              dispatch.common.updateData({
                systemCheckingData: res,
              });
            }
          })
          .catch((e) => {});
      });
    },
  }),
};
