import { REGISTER, LOGIN, GET_INFO_USER, CHANGE_PASS_WORD, GET_DATA_ADMIN_MESSAGE, SET_STATUS_ADMIN } from "client/api/auth";
import { GET_QUESTION_ANSWER_BY_USER, CREATE_QUESTION } from "client/api/common";
import clientUtils from "../utils/client-utils";
// var md5 = require('md5');

export default {
  login(password, username) {
    const object = {
      username,
      password
    };

    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi("post", LOGIN, object)
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  register(body = {}) {
    return new Promise((resolve, reject) => {
      clientUtils
        .requestApi("post", REGISTER, body)
        .then((x) => {
          resolve(x);
        })
        .catch((e) => {
          reject(e);
        });
    });
  },
  getUserInfo() {
    return clientUtils.requestApi(
      "get",
      GET_INFO_USER,
      {}
    );
  },
  changePassWord(body = {}) {
    return clientUtils.requestApi(
      "post",
      CHANGE_PASS_WORD,
      body
    );
  },
  getQuestionData() {
    return clientUtils.requestApi(
      "get",
      `${GET_QUESTION_ANSWER_BY_USER}?limit=10&page=0&sortby=id:desc`,
      {}
    );
  },
  createdQuestionFromUser(body = {}) {
    return clientUtils.requestApi(
      "post",
      CREATE_QUESTION,
      body
    );
  },
  getDataAdminMessage() {
    return clientUtils.requestApi(
      "get",
      `${GET_DATA_ADMIN_MESSAGE}?limit=10&page=0&sortby=id:desc`,
      {}
    );
  },
  setStatusMessage(body = {}) {
    return clientUtils.requestApi(
      "post",
      SET_STATUS_ADMIN,
      body
    );
  },
};
