import lottoProvider from "data-access/game/lotto-provider";
import moment from "moment";
import { convertTimeUTCtoLocal } from "helper/helper";
import {
  convertItemSelected,
  randomNumberMinMax,
  getGameIdNumber,
  converDataHistoryExaclyGame,
} from "helper/game";
import snackbar from "utils/snackbar-utils";

export default {
  state: {
    history: [],
    result: {},
    orderItem: [],
    infoGame: {},
    loading: false,
    endRoundOfGame: "",

    //for main page
    listResult: [],
  },
  reducers: {
    updateData(state, payload = {}) {
      return { ...state, ...payload };
    },
  },
  effects: (dispatch) => ({
    reLoadDataGame(payload = {}) {
      const page = payload?.page || "";

      dispatch.lotto.onGetInfoGame({ ...payload });
      switch (page) {
        case "MAIN": {
          dispatch.lotto.getListRsultGame({ ...payload });
          break;
        }
        default: {
          dispatch.lotto.getDataHistory({ ...payload });
          dispatch.lotto.getResult();
          break;
        }
      }
    },
    getResult() {
      return new Promise((resolve, reject) => {
        lottoProvider
          .getResultLotto()
          .then((res) => {
            if (res?.count) {
              const result = res?.rows[0] || {};

              dispatch.lotto.updateData({
                result: {
                  ...result,
                  data: [
                    result?.number1 || 0,
                    result?.number2 || 0,
                    result?.number3 || 0,
                    result?.number4 || 0,
                    result?.number5 || 0,
                    result?.number6 || 0,
                    result?.number7 || 0,
                  ],
                },
              });
            }

            resolve(true);
          })
          .catch((e) => {
            console.log("getResultPowerballError: ", e);
            reject(false);
          });
      });
    },
    getDataHistory(payload = {}) {
      const type = getGameIdNumber({ ...payload, callAPI: "HISTORY" });

      return new Promise((resolve, reject) => {
        lottoProvider
          .getHistoryLotto(type)
          .then((res) => {
            if (res?.count) {
              const history = (res?.rows || []).map((item) => {
                const dateToLocal = convertTimeUTCtoLocal(
                  item?.createdat || new Date()
                );
                const _resultArr = [
                  item?.resultofgame?.number1 || 0,
                  item?.resultofgame?.number2 || 0,
                  item?.resultofgame?.number3 || 0,
                  item?.resultofgame?.number4 || 0,
                  item?.resultofgame?.number5 || 0,
                  item?.resultofgame?.number6 || 0,
                  item?.resultofgame?.number7 || 0,
                ];
                const _fullbetArr = String(item?.fullbet || "").split(",");
                const exacly = converDataHistoryExaclyGame(
                  _fullbetArr,
                  _resultArr
                );

                return {
                  ...item,
                  timeConvert: moment(dateToLocal).format("HH:mm"),
                  exacly: exacly,
                };
              });

              dispatch.lotto.updateData({
                history: history,
              });
            }
            resolve(true);
          })
          .catch((e) => {
            console.log("getHistoryPowerballError: ", e);
            reject(false);
          });
      });
    },
    onRandomNumberMinMax(isRamdom = true) {
      const option = [];

      while (option.length < 6) {
        const num = randomNumberMinMax(1, 44);
        if (!option.find((_i) => _i.value == num)) {
          option.push({ value: num });
        }
      }

      dispatch.lotto.onSentResultOptions({
        isRamdom: isRamdom,
        option: option,
      });
    },
    onSentResultOptions(result = {}, state) {
      return new Promise((resolve, reject) => {
        const { orderItem = [] } = state?.lotto || {};
        const leg = (orderItem || []).length;
        if (leg > 4) return snackbar.show("5개까지 선택 가능합니다.", "danger");

        const { option = [], isRamdom = false } = result || {};
        if ((option || []).length < 6)
          return snackbar.show("6개의 숫자를 선택해주세요.", "danger");

        const item = convertItemSelected(option, isRamdom, leg);
        dispatch.lotto.updateData({
          orderItem: [...orderItem, ...[item]],
        });
        snackbar.show("구매 대기열에 추가되었습니다.", "success");
        return resolve(isRamdom ? false : true);
      });
    },
    onRemoveItemSelectd(id, state) {
      const { orderItem = [] } = state?.lotto || {};
      const newOrederItem = orderItem.filter((_i) => _i.orderId !== id);

      dispatch.lotto.updateData({
        orderItem: [...newOrederItem],
      });
    },
    resetOrderAndUpdateHistory(payload = {}, state) {
      const { items = [], history = [], type = 0 } = payload;
      const auth = state?.auth || {};
      const arr = [...history];
      console.log("_____arr: ", arr);

      items.map((_his) => {
        arr.unshift({
          ..._his,
          timeConvert: moment().format("HH:mm"),
          username: auth?.auth?.username || "",
        });
      });

      dispatch.lotto.updateData({
        orderItem: [],
        history: [...arr],
      });
      const balance = (auth?.balance || 0) - (items?.length || 0) * type;
      dispatch.auth.updateData({
        balance: balance,
      });
    },
    //get info
    onGetInfoGame(payload = {}) {
      const type = getGameIdNumber({ ...payload, callAPI: "INFO_GAME" });

      return new Promise((resolve, reject) => {
        lottoProvider
          .getInfoMoneyByGameId(type)
          .then((res) => {
            if (res?.id) {
              const dateToLocal = convertTimeUTCtoLocal(
                res?.endroundofgame || new Date()
              );

              dispatch.lotto.updateData({
                infoGame: res,
                endRoundOfGame: moment(dateToLocal).format(
                  "MM-DD-YYYY HH:mm:ss"
                ),
              });
            }

            resolve();
          })
          .catch(reject);
      });
    },
    //bet game
    onBetGameOrder(_payload = {}, state) {
      const typeAPI = getGameIdNumber({ ..._payload, callAPI: "BETGAME" });
      const gameid = getGameIdNumber({ ..._payload });
      const { orderItem = [], history = [] } = state?.lotto || {};
      const payload = orderItem.map((item) => {
        delete item["result"];
        delete item["type"];
        delete item["orderId"];

        return { ...item };
      });

      dispatch.lotto.updateData({ loading: true });
      const body = { items: payload, history: history };
      return new Promise(() => {
        const items = { items: [...(payload || [])] };

        lottoProvider
          .betGameLotto(items, typeAPI)
          .then((res) => {
            dispatch.lotto.updateData({ loading: false });
            const { issuccess = false } = res || {};

            if (issuccess) {
              snackbar.show("주문이 완료되었습니다.", "success");

              dispatch.lotto.resetOrderAndUpdateHistory(
                { ...body, type: Number(_payload?.typeGame || 5000) },
                { ...state }
              );
              dispatch.history.getMyInquireLotto({ gameid: gameid });
              return;
            }

            snackbar.show("주문에 실패했습니다!", "danger");
          })
          .catch(() => {
            snackbar.show(
              "오류가 발생했습니다. 잠시 후 다시 시도해주세요!",
              "danger"
            );
            dispatch.lotto.updateData({ loading: false });
          });
      });
    },

    getListRsultGame(payload) {
      return new Promise((resolve, reject) => {
        lottoProvider
          .getResultLotto({ ...payload })
          .then((res) => {
            resolve(true);

            if (res?.count) {
              const arr = [];
              const result = res?.rows || [];

              result.map((item) => {
                arr.push({
                  ...item,
                  totalBall: 7,
                  data: [
                    item?.number1 || 0,
                    item?.number2 || 0,
                    item?.number3 || 0,
                    item?.number4 || 0,
                    item?.number5 || 0,
                    item?.number6 || 0,
                    item?.number7 || 0,
                  ],
                });
              });

              dispatch.lotto.updateData({
                listResult: arr,
              });
            }
          })
          .catch((e) => {
            reject(false);
          });
      });
    },
  }),
};
