import React from 'react'
import { Menu } from "antd"
import './style.scss'
import { NAV_ITEM } from 'config/nav'
import { useHistory } from 'react-router-dom'
import { RightOutlined, CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons'
import SelectGame from './SelectGame'
// import PropTypes from 'prop-types';
const { SubMenu, Item } = Menu;




const Header = ({
    isLogin
}) => {
    const history = useHistory()
    const { pathname = '' } = history?.location || {}



    const turnOnOffMenuMobile = () => {
        document.getElementById('checkBoxShowMenu').click();
        const checked = document.getElementById('checkBoxShowMenu').checked
        const element = document.getElementById('_header')


        setTimeout(() => {
            if (checked) {

                element.classList.add("mobile-hidden-shadow")
                document.getElementById('bntIconClose').style = "display: block";
                document.getElementById('bntIconBar').style = "display: none";
                return
            }

            element.classList.remove("mobile-hidden-shadow")
            document.getElementById('bntIconClose').style = "display: none";
            document.getElementById('bntIconBar').style = "display: block";
        }, 250)

    }

    const renderMenuForMobile = NAV_ITEM.map((item, index) => {
        if (item?.isLogin == isLogin && item?.isMobile) {
            if (isLogin === false && item?.href == pathname) return null

            if (!(item?.subMenu || []).length) {
                return <Item key={String(index)}>
                    <a href={item?.href || '#'}
                        className='f-14'>
                        <span>{item?.name || ''}</span>
                    </a>
                </Item>
            }

            return (
                <SubMenu key={String(index)}
                    title={item?.name || ''}
                    className="pd-0"
                >
                    {
                        (item?.subMenu || []).map((item2, index2) => {
                            return <Item key={String(index2) + (item2?.name || '')}>
                                <a href={item2?.href || '#'}
                                    className='f-14 d-flex'
                                    style={{ alignItems: 'center' }}
                                >
                                    <RightOutlined style={{ color: "#A3A1A1" }} />
                                    <span>{item2?.name || ''}</span>
                                </a>
                            </Item>
                        })
                    }

                </SubMenu>
            )

        }

    })

    const customExpandIcon = (props) => {
        if (props.isOpen) {
            return <CaretUpOutlined />
        } else {
            return <CaretDownOutlined />
        }
    }

    const showMenuGame = (type = "TURN_ON") => {
        const element = document.getElementById('page-game-list')

        if (type == "TURN_ON") element.style.display = "block";
        else element.style.display = "none";
    }
    return (
        <header>
            <div id="_header" className="header-page flex-item-center header-height border-bottom-border-head">
                <ul className="container-cst justify-between flex-item-center " >
                    <li className="nav-item">
                        <a href="/" className="logo">
                            <img src={require("resources/images/logo.png")} alt="lottery" aria-roledescription="logo" />
                        </a>
                    </li>
                    <li className="nav-item button"
                        onClick={() => turnOnOffMenuMobile()}>
                        <img id="bntIconBar" src={require("resources/images/icon/ic_bar.png")} alt="lottery" aria-roledescription="bar icon" />
                        <span className="close" id="bntIconClose"></span>
                    </li>
                    {
                        NAV_ITEM.map((item, index) => {
                            if (item?.isLogin == isLogin && item?.isDesktop) {
                                if (isLogin === false && item?.href == pathname) return null


                                if (item?.href == '/game') {
                                    return <li className="nav-item before-head text"
                                        key={String(index)}
                                        onMouseOver={() => showMenuGame("TURN_ON")}
                                        onMouseOut={() => showMenuGame("TURN_OFF")}
                                    >
                                        <a href='#' className='f-16 flex-center'>
                                            <span>{item?.name || ''}</span>
                                            <CaretDownOutlined className="f-12 ml-1" />
                                        </a>
                                    </li>
                                }

                                return <li className="nav-item before-head text" key={String(index)} >
                                    <a href={item?.href || '#'}
                                        className='f-16 flex-center'>
                                        <span>{item?.name || ''}</span>
                                        <CaretDownOutlined
                                            className="f-12 ml-1"
                                            style={(item?.subMenu || []).length > 0 ? { display: 'block' } : { display: 'none' }}
                                        />
                                    </a>


                                    {/* sub menu */}
                                    {
                                        (item?.subMenu || []).length > 0 &&
                                        (
                                            <ul className="sub-item-menu">
                                                {item.subMenu.map((item2, index2) => {
                                                    return (
                                                        <li
                                                            // style={{ marginBottom: (index2 + 1) == item.subMenu.length ? 0 : 7 }}
                                                            className="m-0 "
                                                            style={{ lineHeight: '38px' }}
                                                            key={String(index2) + (item2?.name || '')}
                                                        >
                                                            <a href={item2?.href || '#'}
                                                                className='f-16 d-flex'
                                                                style={{ fontWeight: 550, alignItems: 'center' }}
                                                            >
                                                                <RightOutlined className="f-14 mr-1" style={{ color: "#A3A1A1" }} />
                                                                <span>{item2?.name || ''}</span>
                                                            </a>
                                                        </li>
                                                    )
                                                })}
                                            </ul>
                                        )
                                    }

                                </li>
                            }

                        })
                    }
                </ul>
            </div>
            <div className="header-height" />


            {/* mobile menu */}
            <div className="menu-toggle-mobile">
                <div id="menuToggle" >
                    <input hidden type="checkbox" id="checkBoxShowMenu" />
                    <ul id="menu" >

                        <Menu mode="inline" expandIcon={customExpandIcon}>
                            {renderMenuForMobile}
                        </Menu>
                    </ul>
                </div>
            </div>

            {/* menu show game desktop */}
            <SelectGame />
        </header>


    )
}

export default Header